           <!-- 第一版 备份-->
           <!-- <common-checkbox v-model="isShowStoreComment" class="left" :disabled="commentChangeDisable" :defaultSelect="commentChangeDisable" :width="'18px'"  :height="'18px'" @onChange="toggleShopComment"></common-checkbox><span>{{storeId?"只看当前店铺评价":"全部评价"}}</span> -->
           <!-- 第二版 备份-->
           <!-- <span class="comment-filter" :class="{'filter-active':showGoodsComment || showShopComment}" @click="toggleFilter">
            全部评价
            <span class="filter-icon" :class="{'filter-icon-open':filterShow,'filter-icon-close':!filterShow}"></span>
           </span> -->
           <!-- 第三版 -->
           <common-checkbox v-model="showGoodsComment" class="left" :width="'14px'" :height="'14px'" @onChange="toggleShopComment"></common-checkbox><span class="filter-label label-left">只看当前商品</span>
           <common-checkbox v-model="showShopComment" class="left" :width="'14px'" :height="'14px'" @onChange="toggleShopComment" v-if="storeId"></common-checkbox><span class="filter-label" v-if="storeId">只看当前店铺</span>
          </span>
          <span class="title-right">
            好评度 {{positiveRate}}%
          </span>
        </div>
        <ul :class="{expand:expandFlag}">
          <li
            :class="{active:item.selected}"
            :key="index"
            @click="fnChangeCommentType(item,index)"
            v-for="(item,index) in praiseList"
          >{{item.impressionName}}（{{item.labelNum}}）</li>
        </ul>
        <div class="expand" v-if="showExpand">
          <img
            @click="fnExpandPraise"
            alt
            :src="`${$cdnImageUrl}/cart/icon-arrow-down1.png`"
            v-if="!expandFlag"
          />
          <img
            @click="fnExpandPraise"
            alt
            :src="`${$cdnImageUrl}/cart/icon-arrow-up1.png`"
            v-if="expandFlag"
          />
        </div>
      </div>
      <comment-list
        :show="show"
        :commentList="commentList"
        :finished="finished"
        :finishedText="finishedText"
        :vantLoading="vantLoading"
        :hidePic="false"
        @fnShowCommentContent="fnShowCommentContent"
        @onLoad="onLoad"
        v-if="commentList.length"
      ></comment-list>
      <div class="nodata" v-if="commentList.length===0">暂时没有评价</div>
    </div>
     <!-- <common-popup v-model="filterShow" @bgClicked="bgClicked">
       <div class="comment-filter-content">
          <div class="filter-item" :class="{'item-active':showGoodsComment}" @click="goodsComment">仅看当前商品评价</div>
          <div class="filter-item" :class="{'item-active':showShopComment}" @click="shopComment" v-if="storeId">仅看当前店铺评价</div>
       </div>
      </common-popup> -->
      <!-- 返回顶部 -->
      <top-feedBack :isShow="showToTopFlag" @hiddenFeedBack="hiddenFeedBack"  @toTop="fnScrollToTop"></top-feedBack>
  </div>
</template>

<script>
import commentList from '../components/comment-list'
import commonCheckbox from '@/components/checkbox/common-checkbox'
// import commonPopup from '../components/common-popup';
import mixins from '@/components/top-feedBack/mixins_inpage_scroll.js'
import { toAssessList, getProductImpressions, getCommentsByCondition } from '@/http/service/goods'
export default {
  mixins: [mixins],
  components: {
    commentList,
    // commonPopup,
    commonCheckbox
  },
  props: ['proId', 'posRate', 'storeId', 'show'],
  data() {
    return {
      praiseList: [
        {
          labelNum: 0,
          impressionName: '全部',
          commentType: 'all',
        },
        {
          labelNum: 0,
          impressionName: '好评',
          commentType: 'praise',
        },
        {
          labelNum: 0,
          impressionName: '中评',
          commentType: 'neutral',
        },
        {
          labelNum: 0,
          impressionName: '差评',
          commentType: 'poor',
        },
        {
          labelNum: 0,
          impressionName: '有图',
          commentType: 'image',
        },
        {
          labelNum: 0,
          impressionName: '',
        },
      ],
      praiseListBak: [],
      expandFlag: false,
      showExpand: false,
      productId: '',
      positiveRate: 0,
      commentList: [],
      commentType: 'all',
      pageSize: 20,
      pageIndex: 1,
      finished: false,
      finishedText: '没有更多啦~',
      vantLoading: false,
      isShowStoreComment: false,
      commentChangeDisable: false,
      filterShow: false, // 评价筛选弹窗
      showGoodsComment: false, // 筛选商品评价
      showShopComment: false, // 筛选店铺评价
      scrollTarget: '#commentInfo', // 页面滚动元素

    }
  },
  created() {
    this.praiseListBak = this.praiseList // 备份商品印象
    this.productId = this.proId
    this.positiveRate = this.posRate
    // if (!this.storeId) {
    //   // 商品不归属任何店铺则 筛选评价无效
    //   this.isShowStoreComment = true
    //   this.commentChangeDisable = true
    // }
    this.toAssessList()
    this.getCommentsByCondition()
    this.initImpressions() // 初始化商品印象
  },
  methods: {
    // /**
    //  * 返回顶部
    // */
    // fnScrollToTop() {
    //   this.$refs.wrap.scrollIntoView({
    //     block: 'start',
    //     behavior: 'smooth', // 平滑过渡
    //   }) // 回到顶部
    // },
    /**
     * @description 筛选商品评价切换
    */
    goodsComment() {
      this.showGoodsComment = !this.showGoodsComment
      // 筛选条件改变 刷新评价数/买家印象/商品评价
      this.toggleShopComment()
    },
    /**
     * @description 筛选店铺评价切换
    */
    shopComment() {
      this.showShopComment = !this.showShopComment
      // 筛选条件改变 刷新评价数/买家印象/商品评价
      this.toggleShopComment()
    },
    /**
     * @description 评价筛选浮层
    */
    toggleFilter() {
      this.filterShow = !this.filterShow
    },
    bgClicked() {
      this.filterShow = false
      // this.stockPop = false
      // this.currentFilter = ''
    },
    initImpressions() {
      this.getProductImpressions().then(res => {
        if (!res) {
          this.praiseList = this.praiseListBak
        } else {
          this.praiseList = this.praiseListBak.concat(res)
        }
        this.praiseList.forEach((item, index) => {
          if (index < 5) {
            item.selected = false
            if (index === 0) {
              item.selected = true
            }
          }
        })
        if (this.praiseList.length > 6) {
          this.showExpand = true
        } else {
          this.showExpand = false
        }
      })
    },
    /**
     * @description 商品评价 各种类评价的数量
     */
    toAssessList() {
      toAssessList({
        productId: this.productId,
      }).then(res => {
        if (res.success) {
          this.praiseList[0].labelNum = res.data.totalNum // 全部
          this.praiseList[1].labelNum = res.data.positiveNum // 好评
          this.praiseList[2].labelNum = res.data.neutralNum // 中评
          this.praiseList[3].labelNum = res.data.negativeNum // 差评
          this.praiseList[4].labelNum = res.data.hasPicNum // 有图
        } else {
          this.$toast(res.message)
        }
      })
    },
    toggleAssessList() {
      let param = {
        productId: this.productId,
      }
      // if (this.isShowStoreComment) {
      //   param.storeId = this.storeId
      // }
      if (this.showGoodsComment) {
        param.onlyQueryProductId = true
      }
      if (this.showShopComment) {
        param.storeId = this.storeId
      }
      toAssessList(param).then(res => {
        if (res.success) {
          this.praiseList[0].labelNum = res.data.totalNum // 全部
          this.praiseList[1].labelNum = res.data.positiveNum // 好评
          this.praiseList[2].labelNum = res.data.neutralNum // 中评
          this.praiseList[3].labelNum = res.data.negativeNum // 差评
          this.praiseList[4].labelNum = res.data.hasPicNum // 有图
        } else {
          this.$toast(res.message)
        }
      })
    },
    /**
     * @description 商品评价详情接口
     */
    getProductImpressions() {
      return new Promise((resolve, reject) => {
        let param = {
          productId: this.productId
        }
        // if (this.isShowStoreComment) {
        //   param.storeId = this.storeId
        // }
        if (this.showGoodsComment) {
          param.onlyQueryProductId = true
        }
        if (this.showShopComment) {
          param.storeId = this.storeId
        }
        getProductImpressions(param).then(res => {
          if (res.success) {
            resolve(res.data)
          } else {
            resolve(null)
            this.$toast(res.message)
          }
        })
      })
    },
    /**
     * @description 商品评价详情接口
     */
    getCommentsByCondition() {
      return new Promise((resolve, reject) => {
        let param = {
          commentType: this.commentType,
          productId: this.productId,
          pageSize: this.pageSize,
          pageIndex: this.pageIndex,
        }
        // if (this.isShowStoreComment) {
        //   param.storeId = this.storeId
        // }
        if (this.showGoodsComment) {
          param.onlyQueryProductId = true
        }
        if (this.showShopComment) {
          param.storeId = this.storeId
        }
        getCommentsByCondition(param).then(res => {
          if (res.success) {
            this.commentList = this.commentList.concat(this.fnFormatComment(res.data))
            this.finished = res.data.length !== this.pageSize
            resolve()
          } else {
            this.$toast(res.message)
          }
        })
      })
    },
    toggleShopComment() {
      this.toggleAssessList()// 异步刷新 好评/中评。。数
      this.initImpressions()
      this.$emit('toggleShowStoreComment', this.isShowStoreComment)
      let param = {
        commentType: 'all',
        productId: this.productId,
        pageSize: this.pageSize,
        pageIndex: this.pageIndex,
      }
      // if (this.isShowStoreComment) {
      //   param.storeId = this.storeId
      // }
      if (this.showGoodsComment) {
        param.onlyQueryProductId = true
      }
      if (this.showShopComment) {
        param.storeId = this.storeId
      }
      return new Promise((resolve, reject) => {
        getCommentsByCondition(param).then(res => {
          if (res.success) {
            this.commentList = this.fnFormatComment(res.data)
            this.finished = res.data.length !== this.pageSize
            resolve()
          } else {
            this.$toast(res.message)
          }
        })
      })
    },
    /**
     * @description 处理评论内容  超长截取
     */
    fnFormatComment(commentList = []) {
      if (commentList.length === 0) {
        return []
      }
      commentList.forEach(item => {
        // 评论
        if (item.commentContent.length > 71) {
          item.longContentBtn = true
          item.longContent = true
          item.miniContent = item.commentContent.substring(0, 71) + '...'
        } else {
          item.miniContent = item.commentContent
        }
        // 商家回复 storeCommentReplies
        if (item.storeCommentReplies && item.storeCommentReplies.length) {
          const replies = item.storeCommentReplies
          replies.forEach(reply => {
            if (reply.replyContent.length > 71) {
              reply.longBtn = true
              reply.long = true
              reply.miniReply = reply.replyContent.substring(0, 71) + '...'
            } else {
              reply.miniReply = reply.replyContent
            }
          })
        }
        // 用户追评 experienceContent
        if (item.experienceContent && item.experienceContent.length) {
          const edata = item.experienceContent
          if (edata.length > 71) {
            item.longExperienceBtn = true
            item.longExperience = true
            item.miniExperienceContent = item.experienceContent.substring(0, 71) + '...'
          } else {
            item.longExperience = false
            item.miniExperienceContent = item.experienceContent
          }
        }
        // 客服回复 serviceCommentReplies
        if (item.serviceCommentReplies && item.serviceCommentReplies.length) {
          const serviceCommentReplies = item.serviceCommentReplies
          serviceCommentReplies.forEach(serviceCommentReply => {
            if (serviceCommentReply.replyContent.length > 71) {
              serviceCommentReply.longServiceBtn = true
              serviceCommentReply.longService = true
              serviceCommentReply.miniServiceContent = serviceCommentReply.replyContent.substring(0, 71) + '...'
            } else {
              serviceCommentReply.longService = false
              serviceCommentReply.miniServiceContent = serviceCommentReply.replyContent
            }
          })
        }
      })
      return commentList
    },
    fnChangeCommentType(data, index) {
      if (index > 4) {
        return
      }
      this.praiseList.forEach(item => {
        item.selected = false
      })
      this.praiseList[index].selected = true
      this.commentType = data.commentType
      this.pageIndex = 1
      this.commentList = []
      this.finished = false
      this.getCommentsByCondition()
    },
    fnExpandPraise() {
      this.expandFlag = !this.expandFlag
    },
    fnShowCommentContent(data) {
      if (data.type === 'commentContent') {
        // 评论
        this.commentList[data.index].longContent = !this.commentList[data.index].longContent
      } else if (data.type === 'experienceContent') {
        // 追评
        this.commentList[data.index].longExperience = !this.commentList[data.index].longExperience
      } else if (data.type === 'serviceReplyContent') {
        // 客服回复
        this.commentList[data.index].serviceCommentReplies[data.subIndex].longService = !this.commentList[data.index].serviceCommentReplies[data.subIndex].longService
      } else if (data.type === 'replyContent') {
        // 商家回复
        this.commentList[data.index].storeCommentReplies[data.subIndex].long = !this.commentList[data.index].storeCommentReplies[data.subIndex].long
      }
    },
    async onLoad() {
      this.pageIndex++
      this.vantLoading = true
      await this.getCommentsByCondition()
      this.vantLoading = false
    },
    closeModel() {
      this.$emit('closeComment')
    }
  },
}
</script>

