              </div>
            </div>
          </div>
          <!-- 评价 -->
          <div class="comment-list-info">
            <div class="comment-list-desc">
              <span v-if="item.longContent" class="tow-line">{{item.miniContent}}</span>
              <span v-else>{{item.commentContent}}</span>
              <div
