                v-if="item.longContentBtn"
              >{{item.longContent?'展开':'收起'}} <i></i></div>
            </div>
            <!-- 评价图片 -->
            <div class="comment-list-pic" v-if="item.commentPics && item.commentPics.length  && !hidePic">
