                />
              </div>
              <div v-if="collapsible&&item.commentPics.length>3" class="float-number">
                <div class="number">{{item.commentPics.length}}</div>
                <img width='10px' height='10px' src="//cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goodsDetail/tupian.png">
              </div>
            </div>
          </div>
          <!-- 追评 -->
