        <div class="pic-pre-idx"><span>{{imgPreShowIndex+1}}/{{imgPreShowList.length}}</span></div>
        <div class="comment-text" :class="{'fold':!expandStatus}">
          <div class="text-left" >{{commentContent}}</div>
          <div class="text-right" @click="toggleExpand">
            <div class="open" v-if="expandStatus"></div>
            <div class="close" v-else></div>
          </div>
        </div>
        <div class="img-pre-back" @click="preClose"></div>
      </template>
    </van-image-preview>
