<script>
import Vue from 'vue';

import { ImagePreview } from 'vant'
Vue.use(ImagePreview);
export default {
  props: ['commentList', 'finished', 'vantLoading', 'finishedText', 'collapsible', 'hidePic', 'show'], // hidePic 为true时不展示评价与追评的图片
  data() {
    return {
      loading: false,
      prevInstance: null,
      imgPreShow: false, // 图片预览展示状态
      imgPreShowList: [], // 图片预览展示 列表
      imgPreShowIndex: 0,
      commentContent: '', // 当前预览评论 内容
      commentTextShowStatus: false, // 评价预览状态
      expandStatus: false, // 评价折叠状态
    }
  },
  watch: {
    vantLoading(value) {
      if (value === false) {
        this.loading = value
      }
    },
  },
  methods: {
  /**
     * @description 图片预览关闭
    */
    preClose() {
      this.imgPreShow = false
      this.expandStatus = false
    },
    /**
     * @description 切换展开折叠评价
    */
    toggleExpand() {
      this.expandStatus = !this.expandStatus
    },
    onChange(index) {
      this.imgPreShowIndex = index;
    },
    fnShowCommentContent(type, index, subIndex) {
      this.$emit('fnShowCommentContent', {
        type: type,
        index: index,
        subIndex: subIndex,
      })
    },
    // fnPreviewImage(item, index) {
    //   const _urls = []
    //   item.forEach(element => {
    //     _urls.push(element.picUrl)
    //   })
    //   this.prevInstance = ImagePreview({
    //     images: _urls,
    //     startPosition: index,
    //     onClose() {
    //       // do something
    //     },
    //   })
    // },
    /**
     * @description 图片预览
    */
    fnPreviewImage(item, commentText, index) {
      const _urls = []
      item.forEach(element => {
        _urls.push(element.picUrl)
      })
      this.imgPreShowList = _urls
      this.imgPreShow = true
      this.commentContent = commentText
      // this.prevInstance = ImagePreview({
      //   images: _urls,
      //   startPosition: index,
      //   getContainer: document.getElementById('comment-container'),
      //   onClose() {
      //     // do something
      //   },
      // })
    },
    onLoad() {
      this.$emit('onLoad')
    },
  },
  beforeDestroy() {
    if (this.prevInstance) this.prevInstance.close()
  }
}
</script>
